import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useRemoveOneStaffInstitute } from "../../../../hooks/institute/institute-staff-student-api";
import { Link, useNavigate, useParams } from "react-router-dom";
import RemoveStaffConfirm from "./RemoveStaffConfirm";

const StaffCardMenu = ({ onClose, sid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [remove, setRemove] = useState(false);
  const params = useParams();
  const navigate = useNavigate();
  const [removeOneStaffInstitute] = useRemoveOneStaffInstitute();
  const onRemove = () => {
    if (sid) {
      setRemove(false);
      setDisabled((pre) => !pre);
      removeOneStaffInstitute({
        sid: sid,
      })
        .then(() => {
          setDisabled((pre) => !pre);
          navigate(`/q/${params.username}/feed`);
        })
        .catch({});
    }
  };
  return (
    <>
      {remove ? (
        <RemoveStaffConfirm
          onClose={() => setRemove(false)}
          onRemove={onRemove}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <Link
            to={`/q/${params.username}/edit/staff`}
            state={{
              openAs: "INSTITUTE_SIDE",
              sid: sid,
            }}
            style={{
              width: "100%",
            }}
          >
            <h6>{t("edit")}</h6>
          </Link>

          <h6 onClick={() => setRemove(true)}>{t("remove")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default StaffCardMenu;
